import React from "react";
import Slider from "react-slick";
import { Image } from "react-bootstrap"; 
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import christmas1 from "../images/christmas2020-1.jpg"

export default function SimpleSlider() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  return (
    <Slider {...settings} >
      <div style={{height: "500px"}}>
        <Image src={christmas1} style={{width: "200px"}}/>
      </div>
      <div>
        <h3>2</h3>
      </div>
      <div>
        <h3>3</h3>
      </div>
      <div>
        <h3>4</h3>
      </div>
      <div>
        <h3>5</h3>
      </div>
      <div>
        <h3>6</h3>
      </div>
    </Slider>
  );
}